#post-wrapper {
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(3, 1fr);
}


#post-card {
    background-color: #c3073f;
    color: #fff;
    margin-bottom: 3%;
    padding: 2%;
    box-shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
}

#post-card span {
    float: right;
}